.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-canvas{
  position:relative;
  z-index:20;
}

#container{
  display:inline-block;
  width:100%; 
  height:100%;
  margin: 0 auto; 
  background: black; 
  position:relative; 
  border:5px solid black; 
  border-radius: 10px; 
  box-shadow: 0 5px 50px #333}

.img-warzone {
  min-height:100%;
  min-width:100%;
  height:auto;
  width:auto;
  position:absolute;
  top:-100%; bottom:-100%;
  left:-100%; right:-100%;
  margin:auto;
}


#contextMenu {
  position: fixed;
  padding: 2px 5px;
  background: #2B2B2B;
  box-shadow: 0px 2px 10px #999999;
  
